<template>
  <div class="app-page evaluate-submit">
    <page-content class="page-content">
      <div class="product">
        <div class="p-img"><img src="/static/images/category/jz.png"/></div>
        <div class="p-detail">
          <div class="p-detail-title font-ellipsis">橙子橙子天然维C超甜橙子橙子天然维C橙子橙子天然维</div>
          <div class="p-detail-star">
            <div class="star-title">评星</div>
            <div class="star-rank">
              <van-rate v-model="submitData.starValue" :size="20" :count="5" color="#22ac38" void-color="#999999"/>
            </div>
          </div>
        </div>0
      </div>
      <div class="evaluation-area">
        <van-field
          v-model="submitData.evaluate"
          type="textarea"
          placeholder="收到的东西满意吗？写点评论帮助下别人吧"
          rows="1"
          autosize
        />
      </div>
      <div class="upload-title">
        <title-second title="上传照片" :hideRight="true" size="small"></title-second>
      </div>
      <div class="img-area">
        <div class="img" v-for="(item, index) in 3" :key="index">
          <img src="/static/images/commonicon/pz.png"/>
        </div>
      </div>
      <div class="submit-div flex-center">
        <van-button>提交评论</van-button>
      </div>
    </page-content>
  </div>
</template>
<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
export default {
  name: 'evaluateSubmit',
  components: { PageContent, TitleSecond },
  data () {
    return {
      // 提交参数
      submitData: {
        // 评星值
        starValue: 0,
        // 评价内容
        evaluate: ''
      }
    }
  },
  methods: {
  },
  created () {
  }
}
</script>
<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .evaluate-submit{
    font-family: $font-family-default!important;
    .page-content{
      position: relative;
      .product{
        width: 100%;height: 170px;
        padding: 25px 50px;
        display: flex;justify-content: space-between;align-items: center;
        .p-img{
          width: 124px;height: 100%;
          img{width: 100%;height: 100%;}
        }
        .p-detail{
          width: calc(100% - 124px);height: 100%;
          padding-left: 20px;
          display: flex;flex-direction: column;justify-content: center;
          color: $black333333;
          font-weight: 600;
          .p-detail-title{
            width: 100%;
            font-size: 28px;
          }
          .p-detail-star{
            width: 100%;
            margin-top: 20px;
            font-size: 28px;
            display: flex;justify-content: space-between;align-items: center;
            .star-title{width: 80px}
            .star-rank{
              width: calc(100% - 80px);
            }
          }
        }
      }
      .evaluation-area{
        width: 100%;
        /*height: 200px;*/
        margin-top: 20px;
        border-top: 20px solid #f9f9f9;
        border-bottom: 20px solid #f9f9f9;
        /deep/ .van-cell{
          min-height: 200px;
          padding: 20px 30px;
          .van-cell__value{
            /*box-shadow: darkgrey 0px 1px 1px 0;*/
            .van-field__body{
              textarea{
                max-height: 163.7px;
                overflow: scroll;
              }
            }
          }
        }
      }
      .upload-title{
        padding-left: 30px;
        margin-top: 20px;
      }
      .img-area{
        width: 100%;height: 160px;
        padding: 10px 66px;
        margin-top: 20px;
        display: flex;justify-content: space-between;align-items: center;
        .img{
          width: 160px;height: 160px;
          img{width: 100%;height: 100%;}
        }
      }
      .submit-div{
        width: 100%;height: 76px;
        position: absolute;bottom: 60px;
        /deep/ .van-button{
          width: 90%;height: 100%;
          border-radius: 10px;
          background: -webkit-linear-gradient(left top, $green22ac38, $green1a7829); /* Safari 5.1 - 6.0 */
          background: linear-gradient(to bottom right, $green22ac38, $green1a7829);
          color: $whiteffffff;
          line-height: 0;font-weight: 600;
          font-family: $font-family-Bold;
        }
      }
    }
  }
</style>



